import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { GardenPoMasterRoutingModule } from './garden-po-master-routing.module';
import { GardenPoMasterComponent } from './garden-po-master.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    GardenPoMasterRoutingModule,
  ],
  declarations: [
    GardenPoMasterComponent,
  ],
  schemas:[NO_ERRORS_SCHEMA]
})
export class GardenPoMasterModule { }



