import { Component, OnInit, ViewChild, Inject, ComponentFactoryResolver, ElementRef, ViewContainerRef } from '@angular/core';
import { routerTransition } from '../../../router.animations';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxTreeComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxtree';
import { QuickaccessComponent } from '../../../shared';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';

@Component({
  selector: 'app-garden-po-master',
  templateUrl: './garden-po-master.component.html',
  styleUrls: ['./garden-po-master.component.scss']
})

export class GardenPoMasterComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @ViewChild('myWindow') myWindow: jqxWindowComponent;
  @ViewChild('Insert', { read: ViewContainerRef }) Insert: ViewContainerRef;
  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;

  /**
   *  TEST FOR JQX TREE START
   * 
   */

  @ViewChild('myTree') myTree: jqxTreeComponent;

  data: any[] = [
    {
      'id': '2',
      'parentid': '1',
      'text': 'Hot Chocolate',
      'value': '$2.3'
    }, {
      'id': '3',
      'parentid': '1',
      'text': 'Peppermint Hot Chocolate',
      'value': '$2.3'
    }, {
      'id': '4',
      'parentid': '1',
      'text': 'Salted Caramel Hot Chocolate',
      'value': '$2.3'
    }, {
      'id': '5',
      'parentid': '1',
      'text': 'White Hot Chocolate',
      'value': '$2.3'
    }, {
      'text': 'Chocolate Beverage',
      'id': '1',
      'parentid': '-1',
      'value': '$2.3'
    }, {
      'id': '6',
      'text': 'Espresso Beverage',
      'parentid': '-1',
      'value': '$2.3'
    }, {
      'id': '7',
      'parentid': '6',
      'text': 'Caffe Americano',
      'value': '$2.3'
    }, {
      'id': '8',
      'text': 'Caffe Latte',
      'parentid': '6',
      'value': '$2.3'
    }, {
      'id': '9',
      'text': 'Caffe Mocha',
      'parentid': '6',
      'value': '$2.3'
    }, {
      'id': '10',
      'text': 'Cappuccino',
      'parentid': '6',
      'value': '$2.3'
    }, {
      'id': '11',
      'text': 'Pumpkin Spice Latte',
      'parentid': '6',
      'value': '$2.3'
    }, {
      'id': '12',
      'text': 'Frappuccino',
      'parentid': '-1'
    }, {
      'id': '13',
      'text': 'Caffe Vanilla Frappuccino',
      'parentid': '12',
      'value': '$2.3'
    }, {
      'id': '15',
      'text': '450 calories',
      'parentid': '13',
      'value': '$2.3'
    }, {
      'id': '16',
      'text': '16g fat',
      'parentid': '13',
      'value': '$2.3'
    }, {
      'id': '17',
      'text': '45g fat',
      'parentid': '16',
      'value': '$2.3'
    }, {
      'id': '18',
      'text': '67g fat',
      'parentid': '16',
      'value': '$2.3'
    }, {
      'id': '19',
      'text': '13g protein',
      'parentid': '13',
      'value': '$2.3'
    }, {
      'id': '20',
      'text': 'Caffe Vanilla Frappuccino Light',
      'parentid': '12',
      'value': '$2.3'
    }]
  // prepare the data
  treeSource: any = {
    datatype: 'json',
    datafields: [
      { name: 'id' },
      { name: 'parentid' },
      { name: 'text' },
      { name: 'value' }
    ],
    id: 'id',
    localdata: this.data
  };
  // create data adapter & perform Data Binding.
  treeDataAdapter: any = new jqx.dataAdapter(this.treeSource, { autoBind: true });

  // get the tree items. The first parameter is the item's id. The second parameter is the parent item's id. The 'items' parameter represents 
  // the sub items collection name. Each jqxTree item has a 'label' property, but in the JSON data, we have a 'text' field. The last parameter 
  // specifies the mapping between the 'text' and 'label' fields.  
  records: any = this.treeDataAdapter.getRecordsHierarchy('id', 'parentid', 'items', [{ name: 'text', map: 'label' }]);

  selectedData: any;

  getTreeData() {
    this.selectedData = this.myTree.getCheckedItems();
  }

  checkFirst() {
    let items = this.myTree.getItems();
    this.myTree.checkItem(items[0], true);
  }

  checkLast() {
    var items = this.myTree.getItems();
    for (var i = 0; i < items.length; i++) {
      var currentItem = items[i];
      if (currentItem['id'] == "20") {
        this.myTree.checkItem(currentItem.element, true);
      };
    };
  }

  /**
   *  TEST FOR JQX TREE ENDS
   * 
   */

  editrow: number = -1;
  apiUrl: string;
  source: any;
  dataAdapter: any;
  columns: any[];

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    @Inject('API_URL') apiUrl: string,
  ) {
    this.apiUrl = apiUrl;
    this.getList();
  }


  ngOnInit() {
    this.createQuickAccess();
  }
  createQuickAccess() {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
    this.quickAccess.clear();
    const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }
  getList() {
    let myUrl = this.apiUrl + 'grn_po';
    this.source =
      {
        datatype: 'json',
        datafields: [
          { name: 'id', type: 'string' },
          { name: 'created_at', type: 'string' },
          { name: 'updated_at', type: 'string' },
        ],
        id: 'id',
        root: 'data',
        pagesize: 20,
        url: myUrl,
        totalrecords: 100,
        beforeprocessing: (data) => {

        }
      }

    this.dataAdapter = new jqx.dataAdapter(this.source, {
      formatData: (data) => {
        data['page'] = data['pagenum'] + 1;
        data['limit'] = data['pagesize'];
        return data;
      }, downloadComplete: (data, status, xhr) => {
        this.source.totalrecords = data.data.total;
        let tData = data.data.data;
        return tData;
      }, loadError: (jqXHR, status, error) => {
        let messageDiv: any = document.getElementById('listingMessage');
        messageDiv.innerText = error;
        this.winNotification.open();
        this.jqxLoader.close();
      }
    });

    this.columns =
      [
        {
          text: 'S.N', sortable: false, filterable: false, editable: false,
          groupable: false, draggable: false, resizable: false,
          datafield: 'id', columntype: 'number', width: 50,
          cellsrenderer: function (row, column, value) {
            return "<div style='margin:4px;'>" + (value + 1) + "</div>";
          }
        },
        { text: 'created_at', datafield: 'created_at', width: 200 },
        { text: 'updated_at', datafield: 'updated_at', width: 200 },
        {
          text: 'Actions', datafield: 'Edit', columntype: 'button', width: 70,
          cellsrenderer: (): string => {
            return 'Edit';
          },
          buttonclick: (row: number): void => {
            this.editrow = row;
            let dataRecord = this.myGrid.getrowdata(this.editrow);
            this.myWindow.position({ x: '45%', y: '13%' });
            this.myWindow.draggable(false);
            this.myWindow.title('Edit');
            this.myWindow.open();

          }
        }
      ];
  }

}



